import impData from '../models/usersDB';
// import imphelp from '../helpers/signupHelper';

const retrieveUsers = {
  retrieveAllUsers (req, res) {
      const users = impData.fetchAll();
      return res.status(200).send({ status: 200, message: ' Hey !! Hope all users were retrieved Successfully ', data: users });
  }

}
export default retrieveUsers;